export default {
    name: "app-ad",
    props: {
        type: String,
        unitId: String,
        picUrl: String,
        videoUrl: String,

        theme: {
            type: String,
            default() {
                return 'white';
            }
        },


        couponUrl: String,
        couponParams: Object,

    },

    data() {
        return {
            //单屏问题
            rewardedVideoAd: null,
            interstitialAd: null
        }
    },


    created: function () {
        this.init();
    },

    methods: {
        onAdLoad() {},
        onAdPlay() {},
        onAdClose() {},
        onAdError() {},

        init: function () {
            switch (this.type) {
                case 'rewarded-video':
                    this.initRewardedVideoAd();
                    break;
                case 'interstitial':
                    this.initInterstitialAd();
                    break;
                default:
                    break;
            }
        },


        initRewardedVideoAd: function () {
            if (wx.createRewardedVideoAd) {
                this.rewardedVideoAd = wx.createRewardedVideoAd({adUnitId: this.unitId});
                this.rewardedVideoAd.onClose((res) => {
                    if (res && res.isEnded) {
                        this.getUserCoupon();
                    }
                });
            }
        },


        initInterstitialAd: function () {
            if (wx.createInterstitialAd) {
                this.interstitialAd = wx.createInterstitialAd({adUnitId: this.unitId});
            }
        },


        showRewardedVideoAd: function () {
            this.rewardedVideoAd.show().catch(() => {
                this.rewardedVideoAd.load().then(() => this.rewardedVideoAd.show()).catch(err => {
                    uni.showToast({title: err.errMsg, icon: 'none'});
                })
            });
        },


        showInterstitialAd: function () {
            this.interstitialAd.show().catch((err) => {
                uni.showToast({title: err.errMsg, icon: 'none'});
            });
        },


        getUserCoupon() {
            this.$request({
                url: this.couponUrl,
                method: 'POST',
                data: Object.assign({}, this.couponParams)
            }).then(info => {
                if (info.code === 0) {
                    this.$store.dispatch('page/actionSetCoupon', {
                        list: info.data.list,
                        type: 'award'
                    })
                } else {
                    uni.showToast({title: info.msg, icon: 'none'});
                }
            })
        },

    }
}
